* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body{
  font-family: sans-serif;
  background-color: black;
}


.navbar {
    position: relative;
    top: 0;
  width: 100%;
  height: 70px;
  display: flex;
  align-items: center;
  justify-content: space-between;
  background: #000;
  padding: 0px 15px;
}

.logo {
  height: 40px;
}

.avathar {
  height: 40px;
}

.banner {
  width: 100%;
  height: 500px;
  background: linear-gradient(to bottom, #00000000, #000000bd);
  background-position: center;
  background-size: cover;
  color: #fff;
}

.content {
  width: 100%;
  height: 100%;
  display: flex;
  align-items: flex-start;
  justify-content: center;
  flex-direction: column;
  padding-left: 80px;
}

.title {
  font-weight: 900;
  font-size: 50px;
  width: 500px;
}

.banner_button {
  display: flex;
  margin: 10px 0px;
  grid-gap: 10px;
  gap: 10px;
}
.button {
  padding: 6px 25px;
  outline: none;
  border: none;
  background: #ffffff64;
  color: #fff;
  border-radius: 5px;
}

.button i {
  margin-right: 5px;
}

.discription {
  font-size: 16px;
  width: 500px;
  line-height: 25px;
}


.row {
  margin-left: 20px;
  color: #ffff;
}

.posters {
  display: flex;
  padding: 20px;
  overflow-x: scroll;
  overflow-y: hidden;
}

.posters::-webkit-scrollbar {
  display: none;
}

.poster {
  max-height: 250px;
  margin-left: 10px;
}

.smallPoster{
  max-height: 200px;
  margin-left: 10px;
}

